@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&family=Chakra+Petch&display=swap');
header{
  display:flex;
  justify-content:space-between;
  align-items:center;
  text-decoration: none;
  margin-bottom: -30px;
  margin-top: -40px;
}
.logo{
  cursor: pointer;
  font-family: 'Anton', sans-serif;
  font-size: 25px;


}
li{
  display: inline-block;
  padding-left:30px;
}
li>a{
  font-family: 'Chakra Petch', sans-serif;
 font-size: 20px;
 font-weight:600;
 border: 1px solid black;
 background-color:black;
 color:white;
 text-decoration:none;
 padding:5px;
 border-radius: 5px;
}
li>a:hover{
  background-color:white;
  color:black;
}
.G-style{
  font-size:50px;
  font-weight:700;
}
.body{
  padding:2px;
  
