.grid-container {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    grid-gap: 8px;
    padding: 50px;
  }
  
  .grid-container > div {
    background-color: rgb(253, 252, 252);
    text-align: center;
    font-size: 15px;
    font-weight: bold;
    border-radius: 5px;
    padding: 5px;
  }
  .grid-container > div >img{
    width:50%;
    height:50%;
    
  }

  .pdbtn{
      background-color:rgb(71, 39, 39);
      border-radius: 5px;
      padding:7px 13px 7px 13px;
      color:white;
      margin-bottom: 10px;
      border:none
  }
  
